
import React from "react";

function Contact({name,id,section}) {
  return (
    <div className="contact">
      <div class="container">
        <div class="row align-items-center my-5">
          <div class="col-lg-7">
            <img
              class="img-fluid rounded mb-4 mb-lg-0"
              src="https://www.filmibeat.com/ph-big/2019/04/maharshi_155609743740.jpg" 
              alt=""
            />
          </div>
          <div class="col-lg-5">
            <h1>Contact</h1>
            
            <h4>NAME: {name}</h4>
            <h4>ID: {id}</h4>
            <h4>SECTION: {section}</h4>
            
          </div>
          
        </div>
        
      </div>
      
    </div>
  );
}

export default Contact;