
import React from "react";

function Home({Name,Born,Occupation}) {
  return (
    <div className="home">
      <div class="container">
        <div class="row align-items-center my-5">
          <div class="col-lg-7">
            <img
              class="img-fluid rounded mb-4 mb-lg-0"
              src="https://s3.ap-southeast-1.amazonaws.com/images.asianage.com/images/aa-Cover-unddbf59arpn8pbnckilhkkni3-20191027005947.Medi.jpeg"
              alt=""
            />
          </div>
          <div class="col-lg-5">
          <h1>Home</h1>
          
          <h4>NAME: {Name}</h4>
            <h4>Born: {Born}</h4>
            <h4>Occupation:{Occupation}</h4>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;